// All BareCSS component names start with an underscore. Comment out any components you don't need. //

// required components

	// variables !! required [ colours, shadows, fonts, breakpoints ]
	@import "_variables";

	// reset !! required
	@import "_reset";

// optional components

	// anchor link
	@import "_anchor-link";

	// blockquote
	@import "_blockquote";

	// buttons
	@import "_button";

	// card
	@import "_card";

	// code [ code, pre ]
	@import "_code";

	// footer
	@import "_footer";

	// grid
	@import "_grid";

	// input [ input, textarea, checkbox, radio, select ]
	@import "_input";

	// layouts [ img, section, p, hr ]
	@import "_layout";

	// lists [ ul, ol ]
	@import "_list";

	// navigation
	@import "_navigation";

	// table [ table, tr, th, td ]
	@import "_table";

	// tag
	@import "_tag";

	// tooltip
	@import "_tooltip";

	// transitions
	@import "_transition";

	// typography [ h1, h2, h3, h4, h5, h6 ]
	@import "_typography";

	// utility classes
	@import "_utility-attributes";

    body {
		font-family: @font;
	}
	.page-heading {
		font-size: 3rem;
	    margin-top: 0;
	    margin-bottom: 0;
	    padding-bottom: 1.5em;
	    text-align: center;
	    line-height: 1;


	    &:after {
	        content: '';
	        display: block;
	        background: @primary;
	        width: 110px;
	        height: 3px;
	        margin: 20px auto 0;
	    }
	}

	h3, h4 {
		margin: 2rem 0 0.5rem 0;
		font-weight: 500;
	}

	h5 {
		border-bottom: 1px solid @primary;
		padding-bottom: 1rem;
		margin-bottom: 0.7rem;
	}

	a.edit_button {
		background: #aaa;
	}

	.alert-danger {
		background: lighten(@bright-red, 10%);
		border: 5px solid @bright-red;
		color: #fff;
		padding: 10px 8px;
		margin-bottom: 2rem;
	}
