/*	variables
----------------------------------------------------------------------*/
/*	mini browser reset
----------------------------------------------------------------------*/
html {
  font-family: sans-serif;
  font-size: 10px;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
  position: relative;
}
:focus {
  outline: 0;
}
body {
  color: #2e3538;
  margin: 0;
  font-size: 1.4rem;
  line-height: 1.8;
  font-weight: 300;
}
@media (max-width: 50rem) {
  body {
    overflow-x: hidden;
  }
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
b,
strong {
  font-weight: 700;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 2rem;
  font-weight: 200;
}
img {
  border: 0;
}
::selection {
  background-color: #04954a;
  color: #fff;
}
/*	anchor link
----------------------------------------------------------------------*/
a:not([class]),
a[xx] {
  color: inherit;
  text-decoration: none;
  display: inline-block;
  z-index: 1;
}
p a:not([class]):not([btn]):before,
p a[xx]:not([btn]):before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  background: #fd0;
  position: absolute;
  opacity: 0.5;
  transform: scale3d(1, 0.1, 1);
  transform-origin: bottom;
  z-index: -1;
}
p a:not([class]):not([btn]):hover:before,
p a[xx]:not([btn]):hover:before {
  transform: none;
}
/*	blockquote
----------------------------------------------------------------------*/
blockquote:not([class]),
blockquote[xx] {
  margin: 2rem 0;
  padding: 1rem 2rem;
  border-left: 4px solid #04954a;
}
/*	buttons
----------------------------------------------------------------------*/
button:not([class]),
button[xx],
input[type=submit],
[btn] {
  cursor: pointer;
  color: #04954a;
  display: inline-block;
  padding: 1.4rem 2rem;
  background: #fff;
  border: 1px solid #04954a;
  border-radius: 2px;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 1rem;
  margin: 0 1rem 1rem 0;
  -webkit-appearance: none;
}
button:not([class]):before,
button[xx]:before,
input[type=submit]:before,
[btn]:before {
  content: "";
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #04954a;
}
button:not([class])[primary],
button[xx][primary],
input[type=submit][primary],
[btn][primary] {
  border-color: #03773b;
  background: #04954a;
  color: #fff;
  font-weight: 700;
}
button:not([class])[primary]:not(:disabled):hover,
button[xx][primary]:not(:disabled):hover,
input[type=submit][primary]:not(:disabled):hover,
[btn][primary]:not(:disabled):hover {
  background: #03773b;
}
button:not([class])[round],
button[xx][round],
input[type=submit][round],
[btn][round] {
  border-radius: 4rem;
}
@media (max-width: 50rem) {
  button:not([class])[m-full],
  button[xx][m-full],
  input[type=submit][m-full],
  [btn][m-full] {
    width: 100%;
  }
}
button:not([class]):disabled,
button[xx]:disabled,
input[type=submit]:disabled,
[btn]:disabled,
button:not([class]).disabled,
button[xx].disabled,
input[type=submit].disabled,
[btn].disabled {
  border-color: #e3e6e8;
  background: #e3e6e8;
  color: #abb5ba;
  cursor: default;
}
button:not([class])[primary],
button[xx][primary],
input[type=submit][primary],
[btn][primary],
button:not([class])[edit],
button[xx][edit],
input[type=submit][edit],
[btn][edit],
button:not([class])[delete],
button[xx][delete],
input[type=submit][delete],
[btn][delete] {
  padding: 1rem 2rem;
}
button:not([class])[edit],
button[xx][edit],
input[type=submit][edit],
[btn][edit] {
  border-color: #9b9b9b;
  background: #aaa;
  color: #333;
  font-weight: 700;
}
button:not([class])[edit]:not(:disabled):hover,
button[xx][edit]:not(:disabled):hover,
input[type=submit][edit]:not(:disabled):hover,
[btn][edit]:not(:disabled):hover {
  background: #9b9b9b;
}
button:not([class])[delete],
button[xx][delete],
input[type=submit][delete],
[btn][delete] {
  border-color: #840000;
  background: #a30000;
  color: #fff;
  font-weight: 700;
}
button:not([class])[delete]:not(:disabled):hover,
button[xx][delete]:not(:disabled):hover,
input[type=submit][delete]:not(:disabled):hover,
[btn][delete]:not(:disabled):hover {
  background: #840000;
}
/*	card
----------------------------------------------------------------------*/
card {
  background: #fff;
  display: block;
  padding: 2rem;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  margin-bottom: 2rem;
}
card hr:not([class]),
card hr[xx] {
  margin: 1rem 0;
}
card:last-child {
  margin-bottom: 0;
}
card > img:not([class]),
card > img[xx] {
  width: calc(100% + 4rem);
  max-width: none;
  margin: 0 0 2rem -2rem;
  display: block;
}
card > img:not([class]):first-child,
card > img[xx]:first-child {
  margin: -2rem 0 2rem -2rem;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
card > img:not([class]):last-child,
card > img[xx]:last-child {
  margin: 0 0 -2rem -2rem;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}
/*	code
----------------------------------------------------------------------*/
code:not([class]),
code[xx] {
  display: inline-block;
  background: #f4f5f6;
  border: 1px solid #e3e6e8;
  padding: 0 0.5rem;
  color: #454f54;
  font-size: 1.2rem;
  line-height: 1.8;
  font-family: monospace;
  border-radius: 2px;
  text-transform: none;
  font-weight: 300;
}
pre:not([class]) code,
pre[xx] code {
  padding: 2rem;
  border: none;
  border-left: 4px solid #04954a;
  border-radius: 0;
  width: 100%;
  display: block;
}
/*	footer
----------------------------------------------------------------------*/
footer {
  color: #fff;
  width: 100%;
  max-width: 90rem;
  margin: auto;
  padding: 2rem;
  overflow: visible;
}
footer:before {
  content: "";
  background: #04954a;
  width: 102vw;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%, 0);
}
/*	grid
----------------------------------------------------------------------*/
grid {
  display: block;
  font-size: 0;
  width: auto;
  margin: -1rem;
}
grid grid:not(:first-child) {
  margin: 1rem -1rem 0;
}
[col="1/12"] {
  width: 8.33333333%;
}
[col="2/12"],
[col="1/6"] {
  width: 16.66666667%;
}
[col="3/12"],
[col="1/4"] {
  width: 25%;
}
[col="4/12"],
[col="2/6"],
[col="1/3"] {
  width: 33.33333333%;
}
[col="5/12"] {
  width: 41.66666667%;
}
[col="6/12"],
[col="3/6"],
[col="2/4"],
[col="1/2"] {
  width: 50%;
}
[col="7/12"] {
  width: 58.33333333%;
}
[col="8/12"],
[col="4/6"],
[col="2/3"] {
  width: 66.66666667%;
}
[col="9/12"],
[col="3/4"] {
  width: 75%;
}
[col="10/12"],
[col="5/6"] {
  width: 83.33333333%;
}
[col="11/12"] {
  width: 91.66666667%;
}
[col="1/1"] {
  width: 100%;
}
[col] {
  display: inline-block;
  font-size: 1.4rem;
  padding: 1rem;
  vertical-align: top;
}
@media (max-width: 50rem) {
  [col]:not([fx]) {
    width: 100%;
  }
}
/*	inputs
----------------------------------------------------------------------*/
label:not([class]),
label[xx] {
  display: inline-block;
  width: 100%;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 400;
  margin-top: 1rem;
}
label:not([class]):first-child,
label[xx]:first-child {
  margin-top: 0;
}
input:not([type=radio]):not([type=checkbox]):not([type=button]):not([type=submit]):not([class]),
input:not([type=radio]):not([type=checkbox]):not([type=button]):not([type=submit])[xx] {
  display: inline-block;
  background: transparent;
  width: 100%;
  padding: 0.5rem 0 1rem;
  margin-bottom: 2rem;
  border: none;
  border-bottom: 1px solid #e3e6e8;
  font-size: 1.4rem;
  font-weight: 300;
  border-radius: 0;
}
input:not([type=radio]):not([type=checkbox]):not([type=button]):not([type=submit]):not([class]):focus,
input:not([type=radio]):not([type=checkbox]):not([type=button]):not([type=submit])[xx]:focus,
input:not([type=radio]):not([type=checkbox]):not([type=button]):not([type=submit]):not([class]):hover,
input:not([type=radio]):not([type=checkbox]):not([type=button]):not([type=submit])[xx]:hover {
  color: #04954a;
  border-color: #04954a;
  outline: 0;
}
textarea:not([class]),
textarea[xx] {
  display: inline-block;
  background: transparent;
  width: 100%;
  padding: 0.5rem 0 1rem;
  margin-bottom: 2rem;
  border: none;
  border-bottom: 1px solid #e3e6e8;
  font-size: 1.4rem;
  font-weight: 300;
  border-radius: 0;
  min-height: 8rem;
  border-right: 1px solid #e3e6e8;
  padding-right: 2rem;
  border-bottom-right-radius: 0.5rem;
}
textarea:not([class]):focus,
textarea[xx]:focus,
textarea:not([class]):hover,
textarea[xx]:hover {
  color: #04954a;
  border-color: #04954a;
  outline: 0;
}
select:not([class]),
select[xx] {
  padding: 0.5rem 3rem 0.5rem 1rem;
  display: block;
  width: 100%;
  border: 1px solid #e3e6e8;
  background: #fff url('../icons/chevron-down.svg') no-repeat right 1rem center;
  background-size: 2rem;
  border-radius: 2px;
  height: 4rem;
  font-size: 1.4rem;
  font-weight: 300;
  text-indent: 0.5rem;
  margin: 0.5rem 0 2rem;
  appearance: none;
  cursor: pointer;
}
select:not([class]):focus,
select[xx]:focus,
select:not([class]):hover,
select[xx]:hover {
  border-color: #04954a;
  outline: 0;
}
input[type=radio]:not([class]),
input[type=checkbox]:not([class]),
input[type=radio][xx],
input[type=checkbox][xx] {
  z-index: 2;
  opacity: 0;
  margin-right: -2rem;
  width: 2rem;
  height: 2rem;
  vertical-align: middle;
  cursor: pointer;
}
input[type=radio]:not([class]) + label,
input[type=checkbox]:not([class]) + label,
input[type=radio][xx] + label,
input[type=checkbox][xx] + label {
  width: auto;
  text-transform: none;
}
input[type=radio]:not([class]) + label:before,
input[type=checkbox]:not([class]) + label:before,
input[type=radio][xx] + label:before,
input[type=checkbox][xx] + label:before {
  content: "";
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border: 1px solid #abb5ba;
  background: #fff;
  vertical-align: middle;
  margin-right: 1rem;
}
input[type=radio]:not([class]) + label:after,
input[type=checkbox]:not([class]) + label:after,
input[type=radio][xx] + label:after,
input[type=checkbox][xx] + label:after {
  content: "";
  position: absolute;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  width: 1.6rem;
  height: 1.6rem;
  left: 2px;
  top: 4px;
  transform: scale(0);
}
input[type=radio]:not([class]) + label,
input[type=radio][xx] + label {
  font-size: 1.4rem;
  font-weight: 200;
}
input[type=radio]:not([class]) + label:before,
input[type=radio][xx] + label:before {
  border-radius: 100%;
}
input[type=radio]:not([class]):checked + label:before,
input[type=radio][xx]:checked + label:before {
  background: #04954a;
  border-color: #04954a;
}
input[type=radio]:not([class]):checked + label:after,
input[type=radio][xx]:checked + label:after {
  opacity: 1;
  visibility: visible;
  background: #04954a;
  border: 1px solid #fff;
  border-radius: 100%;
  transform: scale(1);
  top: 0.5rem;
}
input[type=checkbox]:not([class]) + label,
input[type=checkbox][xx] + label {
  font-size: 1.4rem;
  font-weight: 200;
}
input[type=checkbox]:not([class]) + label:before,
input[type=checkbox][xx] + label:before {
  border-radius: 2px;
}
input[type=checkbox]:not([class]) + label:after,
input[type=checkbox][xx] + label:after {
  background: url('../icons/done.svg') no-repeat center center;
  border-radius: 100%;
  background-size: 100%;
}
input[type=checkbox]:not([class]):checked + label:before,
input[type=checkbox][xx]:checked + label:before {
  background: #04954a;
  border-color: #04954a;
}
input[type=checkbox]:not([class]):checked + label:after,
input[type=checkbox][xx]:checked + label:after {
  opacity: 1;
  visibility: visible;
  left: 2px;
  top: 0.5rem;
  transform: scale(1);
}
/*	layouts
----------------------------------------------------------------------*/
img:not([class]),
img[xx] {
  height: auto;
  max-width: 100%;
}
section:not([class]),
section[xx] {
  margin: auto;
  padding: 4rem 2rem;
  width: 100%;
  max-width: 90rem;
}
p:not([class]),
p[xx] {
  margin: 0 0 2rem;
}
p:not([class]):last-child,
p[xx]:last-child {
  margin: 0;
}
hr:not([class]),
hr[xx] {
  background: #e3e6e8;
  border: none;
  width: 100%;
  height: 1px;
  margin: 4rem 0;
}
/*	lists
----------------------------------------------------------------------*/
ul:not([class]),
ol:not([class]),
ul[xx],
ol[xx] {
  padding-left: 2rem;
  margin: 0 0 2rem;
}
ul:not([class]) ul,
ol:not([class]) ul,
ul[xx] ul,
ol[xx] ul,
ul:not([class]) ol,
ol:not([class]) ol,
ul[xx] ol,
ol[xx] ol {
  margin-bottom: 0;
}
ul:not([class]),
ul[xx] {
  list-style: disc;
}
ul:not([class]) ul,
ul[xx] ul {
  list-style: circle;
}
ol:not([class]),
ol[xx] {
  list-style: decimal;
}
ol:not([class]) ol,
ol[xx] ol {
  list-style: lower-alpha;
}
/*	navigation
----------------------------------------------------------------------*/
nav:not([class]),
nav[xx] {
  background: #fff;
  color: #2e3538;
  padding: 0 2rem;
  border-bottom: 1px solid #04954a;
  z-index: 10;
  min-height: 4.4rem;
}
nav:not([class])[fx],
nav[xx][fx] {
  background: rgba(255, 255, 255, 0.95);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 11;
}
nav:not([class]) label,
nav[xx] label {
  max-width: 90rem;
  margin: auto;
  display: block;
}
nav:not([class]) label > input,
nav[xx] label > input {
  display: none;
}
nav:not([class]) ul,
nav[xx] ul {
  padding: 0 2rem;
  margin: 0;
  font-size: 0;
  text-align: right;
}
nav:not([class]) ul li,
nav[xx] ul li {
  display: inline-block;
  font-size: 1.4rem;
}
@media (min-width: 50.1rem) {
  nav:not([class]) ul li:hover menu,
  nav[xx] ul li:hover menu,
  nav:not([class]) ul menu:hover,
  nav[xx] ul menu:hover {
    visibility: visible;
    opacity: 1;
    transform: translate3d(0, -1rem, 0);
    pointer-events: auto;
  }
}
nav:not([class]) ul a,
nav[xx] ul a {
  text-decoration: none;
  text-transform: none;
  padding: 0.9rem 1.5rem 0.5rem;
  border-bottom: 4px solid transparent;
  display: block;
  cursor: pointer;
  font-weight: 300;
}
@media (min-width: 50.1rem) {
  nav:not([class]) ul a:not(:only-child),
  nav[xx] ul a:not(:only-child) {
    background-image: url('../icons/chevron-down.svg');
    background-repeat: no-repeat;
    background-position: right 1.5rem;
    background-size: 2rem;
    padding-right: 3rem;
  }
}
nav:not([class]) ul a:hover,
nav[xx] ul a:hover {
  color: #04954a;
  border-color: #04954a;
}
nav:not([class]) menu,
nav[xx] menu {
  padding: 0;
  margin: 0;
  background: #fff;
  z-index: 2;
  text-align: left;
  white-space: nowrap;
  min-width: 100%;
}
@media (min-width: 50.1rem) {
  nav:not([class]) menu,
  nav[xx] menu {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 100%;
    left: 0;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
    transform: translate3d(0, -2rem, 0);
    pointer-events: none;
  }
}
nav:not([class]) menu menuitem,
nav[xx] menu menuitem {
  display: block;
  border-left: 4px solid transparent;
}
nav:not([class]) menu menuitem:hover,
nav[xx] menu menuitem:hover {
  border-color: #04954a;
  background-color: #f4f5f6;
}
nav:not([class]) menu menuitem:hover a,
nav[xx] menu menuitem:hover a {
  transform: translate3d(0.5rem, 0, 0);
}
nav:not([class]) menu menuitem a,
nav[xx] menu menuitem a {
  text-transform: none;
  padding: 0.5rem 2rem 0.5rem 1.6rem;
  border: none;
  font-size: 1.2rem;
}
nav:not([class]) header:before,
nav[xx] header:before,
nav:not([class]) header:after,
nav[xx] header:after {
  content: "";
  position: absolute;
  top: 0;
  right: 1rem;
  width: 4.4rem;
  height: 4.4rem;
  display: none;
  background-size: 3rem;
  background-position: center center;
  background-repeat: no-repeat;
}
nav:not([class]) header:before,
nav[xx] header:before {
  background-image: url('../icons/hamburger.svg');
}
nav:not([class]) header:after,
nav[xx] header:after {
  background-image: url('../icons/close.svg');
  opacity: 0;
  transform: rotate(180deg);
}
nav:not([class]) header a,
nav[xx] header a {
  position: absolute;
  left: 2rem;
  top: 1rem;
  bottom: 1rem;
  cursor: pointer;
  z-index: 2;
  font-size: 1.6rem;
  text-decoration: none;
}
nav:not([class]) header a img,
nav[xx] header a img {
  height: 2rem;
  vertical-align: middle;
  top: -1px;
  margin-right: 1rem;
}
@media (max-width: 50rem) {
  body {
    padding-top: 4.4rem;
  }
  nav:not([class]),
  nav[xx] {
    position: fixed;
    padding: 0;
    top: 0;
    left: 0;
    right: 0;
  }
  nav:not([class]) header,
  nav[xx] header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  nav:not([class]) header:before,
  nav[xx] header:before,
  nav:not([class]) header:after,
  nav[xx] header:after {
    display: block;
  }
  nav:not([class]) label,
  nav[xx] label {
    height: 4.4rem;
  }
  nav:not([class]) label > ul,
  nav[xx] label > ul {
    position: fixed;
    top: 4.4rem;
    right: 0;
    bottom: 0;
    padding: 0;
    transform: translate3d(100%, 0, 0);
    border-top: 1px solid #04954a;
    text-align: left;
    background: #fff;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
    overflow-x: visible;
    overflow-y: auto;
  }
  nav:not([class]) label > ul li,
  nav[xx] label > ul li {
    display: block;
    border-top: 1px solid #e3e6e8;
  }
  nav:not([class]) label > ul a,
  nav[xx] label > ul a {
    padding: 1rem 6rem 1rem 2rem;
    border: none;
  }
  nav:not([class]) label input:checked ~ ul,
  nav[xx] label input:checked ~ ul {
    transform: none;
  }
  nav:not([class]) label input:checked ~ header:before,
  nav[xx] label input:checked ~ header:before {
    transform: rotate(-180deg);
    opacity: 0;
  }
  nav:not([class]) label input:checked ~ header:after,
  nav[xx] label input:checked ~ header:after {
    transform: none;
    opacity: 1;
  }
  nav:not([class]) label menu menuitem,
  nav[xx] label menu menuitem {
    border-color: #e3e6e8;
  }
  nav:not([class]) label menu a,
  nav[xx] label menu a {
    padding-left: 4rem;
  }
}
/*	table
----------------------------------------------------------------------*/
table:not([class]),
table[xx] {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  margin-bottom: 2rem;
  text-align: left;
  overflow: hidden;
}
table:not([class]) th,
table[xx] th,
table:not([class]) td,
table[xx] td {
  border-bottom: 2px solid #e3e6e8;
  padding: 1rem;
}
table:not([class]) th,
table[xx] th {
  font-weight: 700;
  color: #454f54;
}
table:not([class]) th:before,
table[xx] th:before {
  content: "";
  width: 100%;
  height: 10000%;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  background: #f4f5f6;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
}
table:not([class]) th:hover:before,
table[xx] th:hover:before {
  opacity: 1;
  visibility: visible;
}
table:not([class]) td,
table[xx] td {
  border-bottom-width: 1px;
}
table:not([class]) tbody tr:hover,
table[xx] tbody tr:hover {
  background: #f4f5f6;
}
/*	tag
----------------------------------------------------------------------*/
tag {
  display: inline-block;
  font-size: 1.1rem;
  border-bottom: 1px solid #e3e6e8;
  border-radius: 1.5rem;
  background: #f4f5f6;
  color: #04954a;
  padding: 0.2rem 1.2rem;
  margin: 0 0.5rem 0.5rem 0;
}
/*	tooltip
----------------------------------------------------------------------*/
[tt],
[dtt]:disabled,
[att] {
  overflow: visible;
}
[tt]:after,
[dtt]:disabled:after,
[att]:after {
  position: absolute;
  left: 50%;
  top: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 2;
  background: #454f54;
  border-radius: 3px;
  color: #fff;
  font-family: sans-serif;
  font-size: 1.2rem;
  line-height: 2.5;
  font-weight: 300;
  letter-spacing: 0;
  white-space: nowrap;
  padding: 0 1rem;
  transform: translate(-50%, -95%);
  text-transform: none;
}
[tt]:hover:after,
[dtt]:disabled:hover:after,
[att]:hover:after {
  opacity: 0.9;
  visibility: visible;
  transform: translate(-50%, calc(-100% - 5px));
}
[tt]:after {
  content: attr(tt);
}
[dtt]:disabled:after {
  content: attr(dtt);
}
[att]:after {
  content: attr(href);
}
/*	transitions
----------------------------------------------------------------------*/
/* typography
----------------------------------------------------------------------*/
h1:not([class]),
h1[xx] {
  font-size: 4rem;
  line-height: 1.1;
  letter-spacing: -1px;
}
h2:not([class]),
h2[xx] {
  font-size: 3rem;
  line-height: 1.1;
  letter-spacing: -0.05rem;
}
h3:not([class]),
h3[xx] {
  font-size: 2.5rem;
  line-height: 1.3;
  letter-spacing: -0.05rem;
}
h4:not([class]),
h4[xx] {
  font-size: 2rem;
  line-height: 1.3;
  letter-spacing: -0.05rem;
}
h5:not([class]),
h5[xx] {
  font-size: 1.4rem;
  line-height: 1.5;
  letter-spacing: 0rem;
  text-transform: uppercase;
  font-weight: 700;
}
h6:not([class]),
h6[xx] {
  font-size: 1.4rem;
  line-height: 1.5;
  letter-spacing: 0rem;
  text-transform: uppercase;
  font-weight: 400;
}
